import gql from "graphql-tag";
import { withApollo } from 'react-apollo';

const GET_POSTS = gql`
    query allPosts {
      allPosts() {
        id
        title
        votes
        url
        createdAt
      }
      _allPostsMeta {
        count
      }
    }
  `

const App = ({client}) => {
  debugger;
  const someQueryResult = client.query({ query: GET_POSTS});
  return someQueryResult.title ? 'hello user' : 'oops';
}

export default withApollo(App);